import Styles from './order-details.module.css';
import imageDone from './../../images/done.png';
import loader from './../../images/loader.gif';
import { useSelector } from '../../services/hooks/hooks';

function OrderDetails() {
  const { orderNumber, orderRequest} = useSelector(store => store.order);
  return (
    <div className={Styles.order_details}>

      {!orderRequest && <p className='text text_type_digits-large pb-8'> {orderNumber.toString().padStart(6, '0')} </p>}
      {!orderRequest && <p className='text text_type_main-medium pb-15'> </p>}
      {orderRequest ?
        <img className='pt-7 pb-8' src={loader} alt={'...'} /> :
        <img className='pb-15' src={imageDone} alt={''} />
      }
      {!orderRequest && <p className='text text_type_main-small pb-2'>    </p>}
      {!orderRequest && <p className='text text_type_main-small text_color_inactive'>     </p> }      
    </div >
  );
}

export default OrderDetails;