import { useDispatch } from '../../services/hooks/hooks';
import { NavLink, Outlet } from 'react-router-dom';
import Styles from './profile.module.css';
import { logout } from '../../services/actions/user';

export const ProfilePage = () => {
  const dispatch = useDispatch();

  const onClick = (e: React.MouseEvent<HTMLAnchorElement>) => {
    e.preventDefault();
    dispatch(logout());
  }

  return (
    <>
      <div className={Styles.container}>
        <section className={Styles.profile}>
          <nav className={Styles.nav}>
            <NavLink to={''} className={Styles.link} end>
              {({ isActive }) => (
                <span className={isActive ? Styles.active : ''}></span>
              )}
            </NavLink>
            <NavLink to={'orders'} className={Styles.link} end>
              {({ isActive }) => (
                <span className={isActive ? Styles.active : ''}> </span>
              )}
            </NavLink>
            <NavLink to={'/'} className={Styles.link} onClick={onClick}>
              {({ isActive }) => (
                <span className={isActive ? Styles.active : ''}></span>
              )}
            </NavLink>
          </nav>
          <p className={Styles.caption}>    &nbsp;   </p>
        </section>
        <Outlet />
      </div>
    </>
  )
};